/* Browserframe  */
.browser-frame-wrapper {
    position: relative;
    margin: 50px -70px;
}
.browser-frame {
    border-radius: 10px;
    overflow: hidden;
    max-width: 750px;
    background: #fff;
  }
  .browser-frame img {
    max-width: 100%;
  }
  .browser-frame:nth-child(2) {
    position: absolute;
    top: 290px;
    right: 0;
    max-width: 550px;
    float: right;
}
.browser-frame:nth-child(2) .browser-controls {
    height: 40px;
}
.browser-frame:nth-child(3) {
    margin-top: 80px;
    max-width: 800px;
    margin: 96px auto 0 auto;
}
  .browser-controls {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #E6ECEF;
    color: #BEC4C6;
  }
  
  .window-controls {
    flex: 0 0 60px;
    margin: 0 2%;
  }
  .window-controls span {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50px;
    background: #FF8585;
  }
  .window-controls span.minimise {
    background: #FFD071;
  }
  .window-controls span.maximise {
    background: #74ED94;
  }
  
  .url-bar {
    flex-grow: 1;
    margin-right: 2%;
    padding: 5px 5px 0 10px;
    font-family: monospace;
    color: #889396;
    overflow: hidden;
  }
  
  .white-container {
    height: 25px;
    border-radius: 3px;
    font-size: 11px;
    background: white;
  }
  
/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(209, 209, 209, 0.8); 
    box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

