/* all Of [data-cursor="hover"] attributes are transition states*/
/* INIT states */

#cursor {
    position: fixed;
    width: 52px;
    height: 52px;
    opacity: 1;
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    z-index: 999;
    -webkit-transition: all 1.1s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 1.1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
/* onClick action */
.expend{
    -webkit-transform: scale(.7);
            transform: scale(.7);
}

#cursor .wrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;   
}
#cursor .wrapper > span{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#cursor[data-cursor="ring"] .cursor-ring::after, #cursor[data-cursor="hover"] .cursor-hover::after, #cursor[data-cursor="action"] .cursor-action::after{
    /* final state */
    content: "";
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

#cursor .cursor-ring::after{
    /* init state  */
    content: "";
    border: 2px solid #174ffc;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    display:block;
    opacity: 0;    
    -webkit-transform: scale(0.3);    
            transform: scale(0.3);
    -webkit-transition: all .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all .5s cubic-bezier(0.165, 0.84, 0.44, 1);

}

#cursor .cursor-hover::after{
    /* init state  */
    content: "";
    background-color: #174ffc;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    display:block;
    opacity: 0;    
}
#cursor .cursor-hover{
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transition: -webkit-transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: -webkit-transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: transform .5s cubic-bezier(0.165, 0.84, 0.44, 1), -webkit-transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
#cursor[data-cursor="hover"] .cursor-hover{
    -webkit-transform: scale(.4);
            transform: scale(.4);
}

#cursor .cursor-action::after{
    /* init state  */
    content: "";
    background-color: #174ffc;
    color: #fff;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    display:block;
    opacity: 0;    
}
#cursor .cursor-action{
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transition: -webkit-transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: -webkit-transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: transform .5s cubic-bezier(0.165, 0.84, 0.44, 1), -webkit-transform .5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.cursor-action > span{
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 9px;
    color:#fff; /* Action text color */
}
#cursor[data-cursor="action"] .cursor-action{
    /* active state */
    -webkit-transform: scale(.8);
            transform: scale(.8);
}

